<template>
<!-- 文章收藏功能 -->
  <van-icon
    :name="value ? 'star' : 'star-o'"
    :class="{collected: value}"
    :loading="loading"
    @click="onCollect"
  />
</template>
<script>
import { addCollectAPI, deleteCollectAPI } from '../../api/index.js'
export default {
  name: 'CollectArticle',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    // article.art_id  => articleId 文章id
    articleId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      loading: false// 调用接口时的加载状态
    }
  },
  methods: {
    async onCollect () {
      this.loading = true
      try {
        if (this.value) {
          // 已收藏，取消收藏
          await deleteCollectAPI(this.articleId)
        } else {
          // 没有收藏，添加收藏
          await addCollectAPI(this.articleId)
        }

        // 更新视图 主动取反
        // 自定义事件修改数据并不是立即的
        this.$emit('input', !this.value)

        this.$toast.success(!this.value ? '收藏成功' : '取消收藏')
      } catch (err) {
        this.$toast.fail('操作失败，请重试！')
      }
      this.loading = false
    }
  }
}
</script>

<style scoped lang="less">
  .collected{
      color: #ffa500;
  }
</style>
