<template>
  <div class="comment-reply">
    <!-- 头部导航 -->
    <van-nav-bar
      :title="comment.reply_count > 0 ? `${comment.reply_count}条回复` : '暂无回复'"
      left-arrow
      fixed
    >
    <template #left>
        <van-icon name="cross" @click="$emit('close')" />
    </template>
    </van-nav-bar>

    <!-- 内容区域 -->
    <div class="scroll-wrap">
        <!-- 文章评论详情 -->
        <CommentItem :comment="comment"></CommentItem>

        <!-- 评论回复区域 -->
        <van-cell>全部回复</van-cell>

        <CommentList
          ref="CommentList"
          :targetId="comment.com_id"
          type="c"
        ></CommentList>
    </div>

     <!-- 底部评论区域 -->
    <div class="reply-bottom">
        <van-button
        class="reply-btn"
        round
        size="small"
        @click="isPostShow = true"
      >
      写评论
      </van-button>
       <!-- ————————————评论框———————————— -->
      <van-popup
        v-model="isPostShow"
        position="bottom"
      >
        <CommentPost
          :targetId="comment.com_id"
          @post-success="postSuccess"
          :articleId="$route.params.articleId"
        ></CommentPost>
      </van-popup>
      <!-- ————————————评论框———————————— -->
    </div>
  </div>
</template>

<script>
import CommentItem from './comment-item.vue'
import CommentList from './comment-list.vue'
import CommentPost from './comment-post.vue'

export default {
  name: 'CommentReply',
  components: {
    CommentItem,
    CommentList,
    CommentPost
  },
  props: {
    comment: {
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      isPostShow: false // 写评论的弹出层状态
    }
  },
  methods: {
    // logerr () { console.log(this.comment) },
    postSuccess (item) {
      // 关闭弹出层
      this.isPostShow = false
      // 将发布内容显示到列表顶部
      // console.log(item)
      // 通过ref 去调用CommentList 的方法将新的评价 添加到列表的顶端
      this.$refs.CommentList.unshiftComment(item)
    }
  }
}
</script>

