    .van-nav-bar{
        background-color: #fff;

        :deep(.van-nav-bar__title) {
            color: #000;
        }

        .van-icon {
            color: #3296fa;
        }
    }
    .scroll-wrap {
        padding-top: 92px;
        padding-bottom: 88px;
        overflow-y: auto;
        background-color: #fff;
    }
    .reply-bottom {
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 88px;
        border-top: 1px solid #d8d8d8;
        background-color: #fff;
    }
    .reply-btn {
        width: 280px;
        height: 46px;
        line-height: 46px;
