<template>
  <div class="comment-list">
    <!-- 文章评论列表 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
      @load="onLoad"
    >
      <!-- <van-cell v-for="(item, index) in list" :key="index">
        {{ item.content }}
      </van-cell> -->
      <CommentItem
        v-for="(item,index) in list"
        :key="index"
        :comment="item"
        @reply-click="onReplyClick"
      ></CommentItem>
      <!-- @reply-click="onReplyClick" ==>其实就是 @reply-click="$('reply-click',$event)"  此处的$event == comment-->
    </van-list>
  </div>
</template>

<script>
import { getCommentsAPI } from '../../../api/index.js'
import CommentItem from './comment-item.vue'
export default {
  name: 'CommentList',
  components: {
    CommentItem
  },
  props: {
    targetId: { // 父组件传入的id, 可能是文章id也可能是评论id
      type: String,
      default: ''
    },
    totalCount: { // 评论总数
      type: Number,
      default: 0
    },
    type: {
      type: String,
      default: 'a'
    }
    /*
      公共方法调用时，注意！！！！
      如果有一个方法，在你接手之前，别人在方法中写了 一个变量 a
      我们在完成新的业务场景时，引申了一个新的变量 c 的时候
      在不影响原来别人的业务代码的同时如何让这个公共方法修改后 风险最小？
      在设置 c 的时候将原来公共方法中的 a 的值设为该形参的默认值
    */
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      error: false,
      offset: null // 请求下一页的页码，默认为null
    //   totalCount: 0 // 总数据条数
    }
  },
  methods: {
    async onLoad () {
    //   console.log(this.targetId, 'targetId')
      // console.log(this.list)
      try {
        // 1. 请求数据
        const { data: res } = await getCommentsAPI({
          // type: 'a',
          type: this.type, // 获取的是文章评论列表-a 还是 评论回复列表-c
          source: this.targetId, // 源id
          offset: this.offset, // 页码
          limit: 10 // 每页数量
        })
        // console.log(res)
        // 2. 将数据添加到列表中
        this.list.push(...res.data.results)
        // 更新中数据条数
        // this.totalCount = res.data.total_count
        this.$emit('update:totalCount', res.data.total_count)
        // 3. 将加载状态loading 改回false
        this.loading = false
        // 4. 判断是否有后续数据，对finished做一些操作
        if (res.data.results.length) {
          // 如果results中有值, 那么获取下一页的偏移量
          this.offset = res.data.last_id
        } else {
          // 如果results中没有数据了
          this.finished = true // 关闭加载
        }
      } catch (error) {
        // console.log(error)
        this.error = true
        this.loading = false
      }
    },
    unshiftComment (item) {
      this.list.unshift(item.data.new_obj)
    },
    onReplyClick (comment) {
      // console.log(comment)
      this.$emit('reply-click', comment)
    }
  }
}
</script>

