<template>
<!-- 通过loading 属性 对按钮 进行操作 -->
    <van-button
        class="follow-btn"
        type="info"
        color="#3296fa"
        round
        size="small"
        icon="plus"
        v-if="isFollowed"
        @click="onFollow"
        :loading="btnLoading"
    >关注</van-button>
    <van-button
        class="follow-btn"
        round
        size="small"
        v-else
        @click="onFollow"
        :loading="btnLoading"
    >已关注</van-button>
</template>

<script>
import { addFollowAPI, deleteFollowAPI } from '../../api/index.js'
export default {
  name: 'FollowUser',
  // 如果使用 v-model 传递到子组件的参数 默认情况下 使用value 来接收
  // 可以使用 model 来自定义接收的数据
  // 通过 v-model 绑定的变量 有一个默认的事件： input 事件，可以通过该事件实现 数据的更新
  // 通过 model 属性来修改 input 名称
  // model: {
  //   prop: 'isFollowed', // 默认为value
  //   event: 'update-follow' // 默认为input
  // },
  props: {
    // value: {
    //   type: Boolean,
    //   default: true
    // },
    // article.is_followed => isFollowed 是否被关注的状态
    isFollowed: {
      type: Boolean,
      default: true
    },
    // article.aut_id  => userId 用户id
    userId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      btnLoading: false // 调用接口时的状态
    }
  },
  methods: {
    // 取关 关注用户
    async onFollow () {
    //   console.log(this.userId)
      this.btnLoading = true // 请求接口前开启
      try {
        // 通过当前的用户关注状态 来判断 调用哪个接口
        if (this.isFollowed) {
          // 已关注状态 - 执行取消关注
          await deleteFollowAPI(this.userId)
        } else {
          // 未关注状态 - 执行关注
          await addFollowAPI(this.userId)
        }
        // 主动将状态取反
        // this.isFollowed = !this.isFollowed
        // this.$emit('update-follow', !this.isFollowed)
        // this.$emit('input', !this.isFollowed) //v-model 重新定向渲染
        // this.$emit('update-follow', !this.isFollowed)
        this.$emit('update:isFollowed', !this.isFollowed)// .sync 的重新定向更新的方法 update:变量名
      } catch (error) {
        // console.log(error)
        if (error.response && error.response.status === 400) {
          this.$toast('你不能关注你自己')
        } else {
          this.$toast('操作失败，请稍后重试')
        }
      }
      this.btnLoading = false // 请求执行后 关闭
    }
  }
}
</script>

