<template>
  <van-icon
      :name="attitude === 1 ? 'good-job' : 'good-job-o'"
      :class="{liked: attitude === 1}"
      :loading="loading"
      @click="onLike"
    />
</template>

<script>
import { addLikeAPI, deleteLikeAPI } from '../../api/index.js'
export default {
  name: 'LikeArticle',
  props: {
    attitude: { // -1 无状态， 0 不喜欢， 1 喜欢
      type: Number,
      default: -1
    },
    // article.art_id  => articleId 文章id
    articleId: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      loading: false // 调用接口时的加载状态
    }
  },
  inject: {
    articleIdPro: {
      type: String,
      default: ''
    }
  },
  mounted () {
    // console.log(this.articleIdPro, 'inject')
  },
  methods: {
    async onLike () {
      this.loading = true
      let status = this.attitude // 当前的点按状态
      try {
        if (this.attitude === 1) {
          // 已点赞，取消点赞
          status = -1
          await deleteLikeAPI(this.articleId)
        } else {
          // 没有点赞，添加点赞
          status = 1
          await addLikeAPI(this.articleId)
        }

        // 更新视图 主动取反
        // 自定义事件修改数据并不是立即的
        this.$emit('update:attitude', status)
      } catch (err) {
        this.$toast.fail('操作失败，请重试！')
      }
      this.loading = false
    }
  }
}
</script>

<style scoped lang="less">
  .liked {
    color: #e81212;
  }
</style>
