<template>
  <div class="article-container">
    <!-- 导航栏 -->
    <van-nav-bar
      class="page-nav-bar"
      title="黑马头条"
    >
      <template #left>
          <van-icon name="arrow-left" @click="$router.back()" />
      </template>
    </van-nav-bar>
    <!-- /导航栏 -->

    <div class="main-wrap">
      <!-- 加载中 -->
      <div class="loading-wrap" v-if="loading">
        <van-loading
          color="#3296fa"
          vertical
        >
        加载中
        </van-loading>
      </div>
      <!-- /加载中 -->

      <!-- 加载完成-文章详情 -->
      <div v-else-if="article.title" class="article-detail">
        <!-- 文章标题 -->
        <h1 class="article-title">{{ article.title }}</h1>
        <!-- /文章标题 -->

        <!-- 用户信息 -->
        <van-cell class="user-info" center :border="false">
          <van-image
            class="avatar"
            slot="icon"
            round
            fit="cover"
            :src="article.aut_photo"
          />
          <div slot="title" class="user-name">{{ article.aut_name }}</div>
          <div slot="label" class="publish-date">{{ article.pubdate | relativeTime }}</div>

          <!-- 子传父 @update-follow="onUpdateFollow" -->
          <!--
            v-model 双向绑定数据源：
            v-model="article.is_followed"
           -->
           <!--
            使用 .sync 方法 可以实现多个数据的双向绑定
            :isFollowed.sync = "article.is_followed"
             -->
          <FollowUser
            :userId="article.aut_id"
            :isFollowed.sync = "article.is_followed"
          ></FollowUser>
            <!-- @update-follow="onUpdateFollow" -->
        </van-cell>
        <!-- /用户信息 -->

        <!-- 文章内容 -->
        <div ref="article-content" class="article-content markdown-body" v-html="article.content"></div>
        <van-divider>正文结束</van-divider>

        <!-- 评论区域 -->
        <CommentList
          ref="CommentList"
          :targetId="articleId"
          :totalCount.sync="totalCommentCount"
          @reply-click="onReplyClick"
        >
        评论
        </CommentList>
        <!-- 评论区域 -->
      </div>
      <!-- /加载完成-文章详情 -->

      <!-- 加载失败：404 -->
      <div class="error-wrap" v-else-if="errStatus == 404">
        <van-icon name="failure" />
        <p class="text">该资源不存在或已删除！</p>
      </div>
      <!-- /加载失败：404 -->

      <!-- 加载失败：其它未知错误（例如网络原因或服务端异常） -->
      <div class="error-wrap" v-else>
        <van-icon name="failure" />
        <p class="text">内容加载失败！</p>
        <van-button class="retry-btn" @click="loadArticle">点击重试</van-button>
      </div>
      <!-- /加载失败：其它未知错误（例如网络原因或服务端异常） -->
    </div>

    <!-- 底部区域 -->
    <div class="article-bottom">
      <van-button
        class="comment-btn"
        type="default"
        round
        size="small"
        @click="isPostShow = true"
      >
      写评论
      </van-button>
      <!-- ————————————评论框———————————— -->
      <van-popup
        v-model="isPostShow"
        position="bottom"
      >
        <CommentPost
          :targetId="article.art_id"
          @post-success="postSuccess"
        ></CommentPost>
      </van-popup>
      <!-- ————————————评论框———————————— -->

      <van-icon
        name="comment-o"
        :badge="totalCommentCount"
        color="#777"
      />

      <!-- ————————————收藏状态———————————— -->
      <CollectArticle
        v-model="article.is_collected"
        :articleId="article.art_id"
      ></CollectArticle>
      <!-- ————————————收藏状态———————————— -->

      <!-- ————————————点赞功能———————————— -->
      <LikeArticle
        :articleId="article.art_id"
        :attitude.sync="article.attitude"
      ></LikeArticle>
      <!-- ————————————点赞功能———————————— -->

      <van-icon name="share" color="#777777"></van-icon>
    </div>
    <!-- /底部区域 -->

    <!-- 弹出层 - 回复评论 -->
    <van-popup
    v-model="isReplyShow"
    position="bottom"
    :style="{ height: '100%' }"
    >
    <!-- 弹出层里的内容 组件 -->
    <!--
      发现的问题：在切换不同的评论弹出层的时候，发现文章评论详情发生了变化，但是评论回复列表区域还是上一条列表
      van-popup弹出层组件，v-model 特点：
        初始化状态下，v-model状态为false 弹出层内容不渲染
        当v-model为true 时，弹出层渲染数据
        但是在关闭淡出层的时候，只是对弹出层的css 样式 进行隐藏

        解决方案： 我们需要给评论回复弹出层组件添加一个 v-if ，来保证弹出层 关闭时，能够清除内部的所有的dom元素，保证下次打开后 回复列表可以正常请求 列表接口
     -->
    <CommentReply
      v-if="isReplyShow"
      :comment= "currentComment"
      @close="isReplyShow = false"
    ></CommentReply>
    </van-popup>
    <!-- /回复评论 -->
  </div>
</template>

<script>
import { getArticleByIdAPI } from '../../api/index.js'
import { ImagePreview } from 'vant'
import FollowUser from '../../components/follow-user/follow-user.vue'
import CollectArticle from '../../components/collect-article/collect-article.vue'
import LikeArticle from '../../components/like-article/like-article.vue'
import CommentList from './components/comment-list.vue'
import CommentPost from './components/comment-post.vue'
import CommentReply from './components/comment-reply.vue'

export default {
  name: 'article-',
  components: {
    FollowUser,
    CollectArticle,
    LikeArticle,
    CommentList,
    CommentPost,
    CommentReply
  },
  data () {
    return {
      article: {}, // 文章详情
      loading: true, // 文章的加载状态
      errStatus: 0, // 获取请求失败的状态
      // btnLoading: false // 关注按钮加载状态
      totalCommentCount: 0, // 评论的总数
      isPostShow: false, // 控制发布评论的显示状态
      isReplyShow: false, // 控制评论回复的弹出层显示状态
      currentComment: {} // 当前点击的评论内容，需要传给回复评论弹窗层
    }
  },
  props: {
    // article.art_id  => articleId 文章id
    articleId: {
      type: String,
      required: true, // 该参数是否必须
      default: ''
    }
  },
  /*
      在vue项目中，往往会涉及到多层组件的嵌套传参，或者是一个参数需要传递给多个组件：
      vue提供给了一种父组件传递数据给它所包含的所有子(后代）组件的方式，
      provide - 一个函数（方法）父组件通过该函数将数据发送
      inject - 在子组件中，作为一个属性(该属性的写法与props完全相同)，可以接收到provide传递过来的数据

      注意事项：
      如果provide传递的是一个引用数据类型，每个组件都可以修改它堆内存中的数据，但如果对该数据进行
      了修改，后期很难查找到修改源，所以在使用这个provide 和inject进行数据传递时，一定遵守只读原则
      如果说，自己不能保证你使用的数据不会被修改，请使用最基本的组件传参形式
    */
  provide: function () {
    return {
      articleIdPro: this.articleId
    }
  },
  created () {
    this.loadArticle()
  },
  methods: {
    async loadArticle () {
      this.loading = true // 为了循环点击重试，需先把状态改为true 展示加载中
      try {
        const { data } = await getArticleByIdAPI(this.articleId)
        // console.log(data)
        this.article = data.data
        // console.log(this.article)

        // 请求数据成功后，触发制作 图片列表事件 (异步操作，需要等图片加载完成后，在进行此操作)
        setTimeout(() => {
          this.previewImage()
        }, 0)
      } catch (error) {
        if (error.response && error.response.status === 404) {
          this.errStatus = 404
        }
        // this.$toast('请求失败，请稍后重试')
      }
      this.loading = false// 无论请求失败还是成功，都要关闭loading
    },
    previewImage () {
      const articleContent = this.$refs['article-content']
      const images = articleContent.querySelectorAll('img')// 1. 先找到所有的img标签 ,images 是一个伪数组
      //   console.log(images)
      // 2. 创建一个处理后只有图片路径的真数组 - 给ImagePreview 的images 使用
      /*
      [... images] 将伪数组转化为 真数组
      .map(img => img.src) 将数组中的每一项变成url字符串
      */
      const imagesList = [...images].map(img => img.src)
      // 3.给每一张图片 绑定打开图片预览功能 的点击事件
      images.forEach((img, index) => {
        img.onclick = () => {
          ImagePreview({
            images: imagesList, // 预览图片的合集
            startPosition: index// 合集展示的起始图片
          })
        }
      })
    },
    onUpdateFollow (newFollowed) {
      // console.log(newFollowed)
      this.article.is_followed = newFollowed
    },
    postSuccess (item) {
      // 关闭弹出层
      this.isPostShow = false
      // 将发布内容显示到列表顶部
      // console.log(item)
      // 通过ref 去调用CommentList 的方法将新的评价 添加到列表的顶端
      this.$refs.CommentList.unshiftComment(item)
    },
    // 开启评论回复弹出层
    onReplyClick (comment) {
      // console.log(comment, '1')
      // 赋值当前文章评论对象
      this.currentComment = comment || {}
      // console.log(this.currentComment)
      // 显示回复弹出层
      this.isReplyShow = true
    }
  }
}
</script>

